.header {
  height: 87px;
  border-bottom: 1px solid #E4E4E4;
  padding: 0 !important;
}
.header .header-container {
  width: 100%;
  padding: 10px;
  background: white;
}
.header .header-container #basic-navbar-nav {
  background: white;
  width: 100%;
  justify-content: center !important;
}
.header .header-container #basic-navbar-nav .nav-link {
  color: #000000;
  padding: 0 50px;
  font-weight: 400;
  font-size: 16px;
}
@media (max-width: 991px) {
  .header .header-container #basic-navbar-nav .nav-link {
    padding: 10px 0;
  }
}

.footer {
  padding: 0 75px;
  position: absolute;
  bottom: 0;
  width: 100%;
  font-size: 16px;
}
.footer .footer-container {
  border-top: 1px solid #E4E4E4;
  padding-top: 40px;
  padding-bottom: 60px;
  display: flex;
}
.footer .footer-container p {
  margin-bottom: 10px;
}
.footer .footer-container .mail-info {
  margin-bottom: 30px;
}
.footer .footer-container a {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  color: #373737;
}
.footer .footer-container .read-more {
  margin-left: 5px;
}

html,
body {
  height: 100%;
}

.container-fluid {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.row {
  width: 100%;
}
